import { StaticImage } from "gatsby-plugin-image";
import React from "react";

const ServiceStickySection = ({ sectionData, bgGray = true }) => {
  return (
    <section className={`${bgGray === true ? "light-gray-bg" : ""}`}>
      <div className="hero-wrapper sm-flex-column flex gap-2xl">
        <div className="item-start gap-xl scroll-sticky-section ">
          <h2 className="about-h2">{sectionData.title}</h2>
          <img
            src={sectionData.image}
            alt={sectionData.title}
            className="w-100"
            loading="eager"
            decoding="async"
          />
        </div>
        <div className="item-start scroll-sticky-section-cards gap-2xl">
          {sectionData.boxData.map((data, index) => (
            <div
              key={index}
              className="flex-center gap-xl scroll-sticky-section-card"
            >
              <div className="item-center scroll-sticky-section-card-progress">
                <div className="scroll-sticky-section-icon item-center">
                  {data.icon ? (
                    <img
                      src={data.icon}
                      width={50}
                      height={50}
                      alt={data.title}
                    />
                  ) : (
                    <span className="about-h3 font-white">{data.id}</span>
                  )}
                </div>
                {index !== sectionData.boxData.length - 1 && (
                  <StaticImage
                    src="../../styles/assets/images/stocktrading/down_arrow_line.svg"
                    alt="Arrow Line Svg"
                    fetchpriority="high"
                    layout="constrained"
                    quality={75}
                    loading="eager"
                    placeholder="none"
                    className="mt-3"
                  />
                )}
              </div>
              <div className="scroll-sticky-content-section-card item-start gap-sm">
                {data.title && (
                  <p className="about-h5 fw-600 font-black">{data.title}</p>
                )}
                <p className="about-p-two">{data.desc}</p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default ServiceStickySection;
